.work-content {
  transition: 1s all;
  width: 60%;
  height: 90%;
  margin: 1%;
  position: relative;

  overflow: auto;
  scroll-snap-type: y mandatory;
}
.work-content::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media screen and (max-width: 601px) {
  .work-content {
    transition: 1s all;
    width: 100%;
  }
  .project-card {
    height: 500px;
  }
}
.project-card {
  scroll-snap-align: start;
  transition: 1s all;

  background-image: url("../../images/paper.jpg");
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 5px 25px 4px #353535;
  margin: 5%;
  padding: 2%;
  height: 90%;
}
.image {
  background-color: #c0c1bf;
  width: 130px;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: rotate(20deg);
}
.img-cont {
  /* border: 1px solid green; */
  float: left;
  width: 150px;
  height: 170px;
}
.image :nth-child(2) {
  background-color: #35342d;
  width: 88%;
  height: 100px;
  margin: 7%;
}
.clip {
  background-image: url("../../images/clip.png");
  position: absolute;
  margin: -15px 0 0 -50px;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 60px;
  height: 60px;
}
.header-cont {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  align-items: flex-start;
}
.proj-info {
  height: 100%;
  text-align: end;
  overflow: scroll;
}
.work-content p {
  line-height: 25px;
  margin: 50px 0 0 0;
  text-decoration: underline;
}
.work-content span {
  line-height: 25px;
  background-color: #70706983;
  border-radius: 4px;
  padding: 5px;
  margin: 5px;
}
address {
  margin: 100px 0 0 0;
}
